import { Component } from 'react';
import ClassTimers from './ClassTimers.jsx';

class ClassTimeBlock extends Component {
	constructor(props) {
		super(props);

		this.state = {};
	}

	render() {
		return (
			<li className="short class time-line-block">
				<div className="class-description">
					<h2 className="class-name"> {this.props.name} </h2>
					<ClassTimers start={this.props.start} finish={this.props.finish}/>
				</div>
			</li>
		)
	}
}

export default ClassTimeBlock