import { Component } from "react";
import "./App.css";
import NavBar from "./components/NavBar.jsx";
import TimeTable from "./components/dayView/TimeTable.jsx";
import "./main.css";
import "./style.css";
import "./root.css";

class App extends Component {
	constructor(props) {
		super(props);

		this.state = { colorTheme: "light-theme" };

		this.switchTheme = this.switchTheme.bind(this);
	}

	switchTheme() {
		this.setState(
			(state) => ({
				colorTheme:
					state.colorTheme === "dark-theme"
						? "light-theme"
						: "dark-theme",
			}),
			() => {
				if (this.state.colorTheme === "dark-theme")
					document.body.classList.add("dark-theme");
				else document.body.classList.remove("dark-theme");
			}
		);
	}

	render() {
		return (
			<div className="App">
				<NavBar onThemeSwitch={this.switchTheme} />
				<TimeTable
					classes={[
						{ name: "·Éõ·Éê·Éó·Éî·Éõ·Éê·É¢·Éò·Éô·Éê", start: "09:00", finish: "09:35" },
						{ name: "·É•·Éê·É†·Éó·É£·Éö·Éò", start: "09:40", finish: "10:15" },
						{ name: "·Éò·É°·É¢·Éù·É†·Éò·Éê", start: "10:20", finish: "10:55" },
						{ name: "·É•·Éò·Éõ·Éò·Éê", start: "11:00", finish: "11:35" },
					]}
				/>
				<NavBar onThemeSwitch={this.switchTheme} />
			</div>
		);
	}
}

export default App;
