import React, { Component } from 'react';
import ClassTimeBlock from "./ClassTimeBlock.jsx";

class TimeTable extends Component{
	constructor (props) {
		super(props);

		this.state = {};
	}

	componentDidMount() {}

	componentWillUnmount() {}
	
	render () {
		return (
			<ul className="time-table-timeline">
				{
					this.props.classes.map(entry => (
						<ClassTimeBlock 
							key={entry.name+entry.start} 
							name={entry.name} 
							start={entry.start} 
							finish={entry.finish}/>
					))
				}
			</ul>
		);
	}
};

export default TimeTable;
